<template>
    <div class="container">
        <div class="portlet bordered">
            <div class="portlet-title">
                <div class="caption">
                    <i class="el-icon-setting font-red"></i>
                    <span class="blod font-red">住户信息管理</span>
                </div>
            </div>
            <div class="portlet-body">
                <div class="table-toolbar">
                    <el-form :inline="true" :model="filters" class="demo-form-inline">
                        <el-form-item>
                            <el-input v-model="filters.name" placeholder="名称"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" v-on:click="getUsers">查询<i
                                    class="el-icon-search el-icon--right"></i>
                            </el-button>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="success" @click="handleAdd">添加规格<i
                                    class="el-icon-plus el-icon--right"></i>
                            </el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div class="table-body">
                    <!--表格-->
                    <el-table
                            @sort-change="sortChange"
                            :data="roomCategoryTable"
                            border
                            style="width: 100%">
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="id"
                                label="序号"
                                width="180">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="name"
                                label="名称">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="phoneNumber"
                                label="电话">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="idcard"
                                label="身份证号">
                        </el-table-column>
                        <el-table-column
                                sortable="custom"
                                align="center"
                                prop="address"
                                label="地址">
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="edit"
                                label="编辑"
                                width="130">
                            <template slot-scope="scope">
                                <el-button  @click="handleEdit(scope.$index, scope.row)" type="primary">
                                    编辑
                                </el-button>
                            </template>
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="delete"
                                label="删除"
                                width="130">
                            <template slot-scope="scope">
                                <el-button  @click.native.prevent="deleteRow(scope.$index, scope.row)"
                                           type="danger">删除
                                </el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <!--分页-->
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10, 15, 20]"
                            :page-size="pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="totalPages">
                    </el-pagination>

                    <!--编辑页面-->
                    <el-dialog title="编辑" :visible.sync="editFormVisible">
                        <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
                            <el-form-item label="名称" prop="name">
                                <el-input v-model="editForm.name" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="电话">
                                <el-input v-model="editForm.phoneNumber" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="身份证号">
                                <el-input v-model="editForm.idcard"></el-input>
                            </el-form-item>
                            <el-form-item label="地址">
                                <el-input v-model="editForm.address"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="editFormVisible = false">取 消</el-button>
                            <el-button type="primary" @click.native="editSubmit" >确 定
                            </el-button>
                        </div>
                    </el-dialog>

                    <!--新增页面-->
                    <el-dialog title="编辑" :visible.sync="addFormVisible">
                        <el-form :model="addForm" label-width="80px" :rules="editFormRules" ref="editForm">
                            <el-form-item label="名称" prop="name">
                                <el-input v-model="addForm.name" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="电话">
                                <el-input v-model="editForm.phoneNumber" auto-complete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="身份证号">
                                <el-input v-model="editForm.idcard"></el-input>
                            </el-form-item>
                            <el-form-item label="地址">
                                <el-input v-model="editForm.address"></el-input>
                            </el-form-item>
                        </el-form>
                        <div slot="footer" class="dialog-footer">
                            <el-button @click="addFormVisible = false">取 消</el-button>
                            <el-button type="primary" @click="addSubmit" >确 定</el-button>
                        </div>
                    </el-dialog>


                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as api from "../api/api"
    import * as RestCode from "../common/RestCode"

    export default {
        name: "CustomerPage",
        data() {
            return {
                // 表格内容
                roomCategoryTable: [],
                // toolbar
                filters: {
                    name: '',
                },
                currentPage: 1,// 当前页
                pageSize: 5, // 每页显示条目个数
                totalPages: 5,
                orderColumn: "id",
                //ascending, descending
                order: "ascending",

                editFormVisible: false,//编辑界面是否显示
                editFormRules: {
                    name: [
                        {required: true, message: '请输入姓名', trigger: 'blur'}
                    ]
                },
                //编辑界面数据
                editForm: {
                    id: 0,
                    name: '',
                    phoneNumber: '',
                    idcard: '',
                    address: '',
                },
                addFormVisible: false,//新增界面是否显示
                addFormRules: {
                    name: [
                        {required: true, message: '请输入姓名', trigger: 'blur'}
                    ]
                },
                // 新增页面数据
                addForm: {
                    id: 0,
                    name: '',
                    phoneNumber: '',
                    idcard: '',
                    address: '',
                },
            }
        },
        methods: {
            // 分页
            handleSizeChange(val) {
                // console.log(`每页 ${val} 条`);
                this.pageSize = val;
                this.getUsers();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getUsers();
            },
            sortChange: function (column) {
                // console.log("column", column, column.prop, column.order)
                this.orderColumn = column.prop;
                this.order = column.order;
                // console.log("data", this.orderColumn, this.order)
                this.getUsers()
            },
            //获取用户列表
            getUsers() {
                let params = new URLSearchParams();
                params.append('currentPage', this.currentPage - 1);
                params.append('pageSize', this.pageSize);
                params.append('orderColumn', this.orderColumn);
                params.append('order', this.order === 'ascending' ? 'asc' : 'desc');
                params.append('userName', this.filters.name);
                api.userList(params).then((res) => {
                    // console.log(this.totalPages)
                    this.totalPages = res.data.data.totalElements;
                    this.roomCategoryTable = res.data.data.content;
                    // console.log("tableData", this.tableData)

                });
            },

            // 显示新增
            handleAdd: function () {
                this.addFormVisible = true;
            }
            ,
            // 新增
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        api.requestRegister(this.addForm).then((res) => {
                            // console.log(this.addForm)
                            let code = res.code;
                            // let msg = res.msg;
                            if (code === RestCode.SUCCESS) {
                                this.$message.success('添加成功！');
                                this.addForm = {};
                            }
                            else if (code === RestCode.RECORD_ALREADY_EXISTS) {
                                this.$message.warning('用户已存在！');
                            }
                            else {
                                this.$message.error('添加失败！');
                            }
                        });
                        this.addFormVisible = false;
                        // this.$refs['addForm'].resetFields();
                        this.getUsers();

                    }
                });
            },

            // 删除
            deleteRow: function (index, row) {
                // console.log(index,row)
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    api.removeUser(row.userId).then((res) => {
                        // console.log(res)
                        let code = res.data.code;
                        // console.log(code)
                        if (code === RestCode.SUCCESS) {
                            this.$message.success('删除成功!');
                            this.getUsers();
                        } else if (code === RestCode.RECORD_NOT_FOUND) {
                            this.$message.warning('用户不存在!');
                        }
                        else if (code === RestCode.FAIL) {
                            this.$message.error('删除失败!');
                        }
                        else {
                            this.$message.info('已取消操作!');
                        }
                    });
                }).catch(() => {
                    this.$message.info('已取消操作!');
                });
            },

            // 显示编辑
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //编辑
            editSubmit: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('确认提交吗？', '提示', {}).then(() => {
                            let para = Object.assign({}, this.editForm);
                            api.editUser(para).then((res) => {
                                this.$message.success('提交成功！');

                                this.$refs['editForm'].resetFields();
                                this.editFormVisible = false;
                                this.getUsers();
                            });
                        });

                    }
                });
            },

        },
        mounted: function () {
            this.getUsers();
        }
    }

</script>

<style scoped>
    .container {
        padding: 0 15px;
    }

    .portlet {
        margin-top: 0;
        padding: 0;
        margin-bottom: 25px;
    }

    .bordered {
        border: 1px solid #e7ecf1 !important;
    }

    .portlet-title {
        padding: 17px 20px 10px;
        margin-bottom: 0;
        border-bottom: 1px solid #e7ecf1;
        min-height: 48px;
    }

    .caption {
        padding: 10px 0;
        float: left;
        font-size: 18px;
        display: inline-block;
        line-height: 18px;
    }

    .caption::after {
        clear: both;
        content: "";
        display: table;
    }

    .caption i {
        margin-right: 5px;
        font-weight: 300;
    }

    .blod {
        font-weight: 700 !important;
        font-size: 16px;
    }

    .font-red {
        color: red;
    }

    .el-row {
        float: right;
    }

    .el-row::after {
        clear: both;
        content: "";
        display: table;
    }

    .portlet-body {
        padding: 10px 20px 20px;
    }

    .table-toolbar {
        margin-bottom: 20px;
        margin-top: 20px;
        padding-top: 10px;
    }

    .el-pagination {
        margin-top: 20px;
        text-align: right;
    }

</style>
